// General flows.
import RemoteCall from './RemoteCall'
import LocalOperation from './LocalOperation'
import LocalNegotiation from './LocalNegotiation'
import RemoteNegotiation from './RemoteNegotiation'
import remoteOnly from './remoteOnly'

// Special-case flows.
import createDirect from './DirectTransfer'
import createConsult from './ConsultativeTransfer'
import createJoin from './Join'

export default function setupFlows(container) {
  return {
    RemoteCall: RemoteCall(container),
    LocalOperation: LocalOperation(container),
    LocalNegotiation: LocalNegotiation(container),
    RemoteNegotiation: RemoteNegotiation(container),
    remoteOnly: remoteOnly(container),

    directTransfer: createDirect(container),
    consulTransfer: createConsult(container),
    join: createJoin(container)
  }
}
